.crafted {
    margin: 165px 0 156px;
    display: flex;
    align-items: flex-end;
    @media (max-width: 768px) {
        flex-direction: column;
        row-gap: 50px;
        margin: 80px 0 80px;
    }
}
.crafted__info {
    max-width: 228px;
    margin-right: 115px;
    @media (max-width:999px) {
        margin-right: 0;
    }
    @media (max-width: 768px) {
        max-width: none;
    }
}
.crafted__info h2 {
    font-weight: 600;
    font-size: 34px;
    line-height: 41px;
    margin-bottom: 27px;
    @media (max-width: 499px) {
        font-size: 24px;
        line-height: 31px;
    }
}
.crafted__info p {
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: #6A6A6A;
    margin-bottom: 24px;
}
.black__btn{
    background: #2F2F2F;
    border-color: #2F2F2F;
}
.black__btn:hover {
    border-color: #F9BF29;
}
//////
.crafted__doods {
    display: flex;
    justify-content: space-between;
    width: 100%;
}
.crafted__doods--item {
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.crafted__doods--item:last-child{
    display: flex;
    @media (max-width: 499px) {
        display: none;
    }
}

.crafted__doods--item__title {
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    margin-top: 45px;
}
.crafted__doods--item__price {
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
}